import React, { Component } from 'react'
import { Button, WhiteSpace, WingBlank, Toast, InputItem } from 'antd-mobile'
import TimerButton from '../modules/TimerButton'
import { onSendMessage, onLogin } from './request'
import './Login.css'

class Login extends Component {

  constructor (props) {
    super(props)
    this.state = {
      loginId: '',
      password: '',
      verifyCode: '',
      check: false, // &&
      firstGetCode: true,
    }
  }

  render () {
    console.log(this)
    // const { from } = this.props.location.state || { from: { pathname: '/' } }
    // console.log('form',from)
    const {loginId, password, verifyCode, check, firstGetCode} = this.state
    let getCode = !!(this.state.loginId && this.state.password)
    let disabledLogin = !(this.state.loginId && this.state.password && this.state.verifyCode)
    return (
      <div className="login">
        <WingBlank>
          <WhiteSpace size="xl"/>
          {/* we data */}
          <div className="logo">
            <span className="main-color">We</span><span>Data</span>
          </div>
          <WhiteSpace/>
          {/*  */}
          <InputItem
            placeholder=""
            value={loginId}
            onChange={this.onValueChange.bind(this, 'loginId')}
          >
            <span className="icon iconfont icon-profile"/>
          </InputItem>
          <WhiteSpace/>
          {/*  */}
          <InputItem
            type="password"
            placeholder=""
            value={password}
            onChange={this.onValueChange.bind(this, 'password')}
          >
            <span className="icon iconfont icon-lock"/>
          </InputItem>
          <WhiteSpace/>
          {/*  */}
          <InputItem
            type="number"
            placeholder=""
            value={verifyCode}
            extra={<TimerButton
              timerCount={60}
              enable={getCode}
              check={check}
              ref={ele => this.TimerButton = ele}
              timerTitle={firstGetCode ? '' : ''}
              onclick={this.onGetNumberCode.bind(this)}
            />}
            onChange={this.onValueChange.bind(this, 'verifyCode')}
          >
            <span className="icon iconfont icon-key"/>
          </InputItem>
          <WhiteSpace size="xl"/>
          <Button type="primary"
                  disabled={disabledLogin}
                  onClick={this.onLogin.bind(this)}
          ></Button>
          <div className="tip">OAOA</div>
        </WingBlank>
      </div>
    )
  }

  componentDidMount () {}

  componentWillMount () {}

  componentWillUnmount = () => {}

  // 
  onValueChange (key, value) {
    this.setState({[key]: value})
  }

  // 
  onLogin () {
    let data = `loginId=${this.state.loginId}&password=${this.state.password}&verifyCode=${this.state.verifyCode}`
    onLogin(data).then(v => {
      if (~v.code && v.data) {
        let token = v.data.token
        localStorage.setItem('token', token)
        document.cookie = `token=${token}`
        this.props.token(true)
      } else {
        Toast.info(v.msg, 1.5)
      }
    })
  }

  // 
  onGetNumberCode () {
    let data = `loginId=${this.state.loginId}&password=${this.state.password}`
    onSendMessage(data).then(v => {
      if (~v.code && v.data) {
        this.setState({firstGetCode: false, check: true})
        this.TimerButton.shouldStartCountting(true)
      } else {
        Toast.info(v.msg, 1.5)
      }
    })
  }

}

export default Login
