import { Toast } from 'antd-mobile'

export default async function wyFetch (...args) {
  const response = await fetch(...args)
  if (!response.ok) {
    Toast.info('', 1.5)
    return
  }
  try {
    const result = await response.json()
    let flag = result.message && (result.message).indexOf('') !== -1
    if (result.code === -1 && (result.msg === 'notLogin' || flag)) {
      Toast.info('', 1.5)
      // 
      return result
    }
    return result
  } catch (e) {}
}
